import moment from "moment";
import React, { useMemo } from "react";
import Linkify from "react-linkify";

import { ApiJobNote } from "@common/api/v1.1/types/jobs";
import { ApiUser } from "@common/api/v1.1/types/users";

type Props = Readonly<{
  canDelete: boolean;
  canEdit: boolean;
  onDelete: (noteId: string) => Promise<void>;
  onEdit: (noteId: string) => void;
  note: ApiJobNote;
  user: ApiUser;
}>;

const CONFIRM_DELETE = "Are you sure you want to delete this note?";

export const NoteContent = ({ canDelete, canEdit, onDelete, onEdit, note, user }: Props) => {
  const [displayContent, displayName, displayDateString] = useMemo(() => {
    let name = note.owner.name;
    if (user.id === note.owner.id) {
      name = "You";
    }
    let dateString = moment
      .unix(note.edited ? note.edited : note.created)
      .format("MMM Do YYYY \\a\\t hh:mm A");
    if (note.edited) {
      dateString = `Updated: ${dateString}`;
    }
    const split = note.content
      .split("\n")
      .map(s => s.trim())
      .filter(s => s.length > 0);
    const content = (
      <Linkify
        componentDecorator={(href: string, text: string, key: number) => {
          return (
            <a key={key.toString()} href={href} target="_blank">
              {text}
            </a>
          );
        }}
      >
        {split.map((s, i) => (
          <p key={i.toString()}>{s}</p>
        ))}
      </Linkify>
    );
    return [content, name, dateString];
  }, [note, user]);
  return (
    <>
      <div className="note-header">
        <h5>{displayName} wrote:</h5>
        {canEdit && (
          <a
            className="hidden-action"
            onClick={e => {
              e.preventDefault();
              onEdit(note.id);
            }}
            href="#"
          >
            Edit
          </a>
        )}
        {canDelete && (
          <a
            className="hidden-action text-danger"
            onClick={e => {
              e.preventDefault();
              if (!window.confirm(CONFIRM_DELETE)) {
                return;
              }
              onDelete(note.id);
            }}
            href="#"
          >
            Delete
          </a>
        )}
      </div>
      <div className="note-content">{displayContent}</div>
      <small className="text-muted">{displayDateString}</small>
    </>
  );
};
NoteContent.displayName = "NoteContent";
